import React from "react";
import styled from "styled-components";

export enum ResponseType {
  RenderSuccess = "RenderSuccess",
  IdError = "IdError",
  ParseError = "ParseError",
  IndexError = "IndexError",
}

export const ErrorDescription = (props: {
  errorMessage: string;
  responseState: ResponseType;
}) => {
  switch (props.responseState) {
    case ResponseType.RenderSuccess:
      return <div />;
    case ResponseType.ParseError:
      return (
        <ErrorType>
          UnexpectedToken: <ErrorMessage>{props.errorMessage}</ErrorMessage>
        </ErrorType>
      );
    case ResponseType.IdError:
      return (
        <ErrorType>
          Name Not Found: <ErrorMessage>{props.errorMessage}</ErrorMessage>
        </ErrorType>
      );
    case ResponseType.IndexError:
      return (
        <ErrorType>
          IndexError: <ErrorMessage>{props.errorMessage}</ErrorMessage>
        </ErrorType>
      );
  }
};

const ErrorType = styled.p`
  width: 80%;
  margin-left: 10%;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
  text-align: center;
  background-color: darkslategrey;
  color: ${(props) => props.color};
  font-size: 1.7em;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
  color: cornsilk;
`;

const ErrorMessage = styled.span`
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
  background-color: lightsteelblue;
  color: ${(props) => props.color};
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 1em;
  padding-right: 1em;
  color: crimson;
`;
