import AceEditor, { IMarker } from "react-ace";
import axios from "axios";
import React, { useState, useEffect, useRef, useCallback } from "react";
import FileSaver from "file-saver";
import "./theme";
import {
  StateText,
  Space,
  Title,
  SubTitle,
  VimBox,
  Button,
  ButtonBox,
  TopBox,
} from "./Components";

import { ErrorDescription, ResponseType } from "./Error";

import WSCMode from "./mode.js";
import template from "./template";
import { displayError } from "./utils";
import Settings from "../settings";

import "ace-builds/src-noconflict/mode-elixir";
import "ace-builds/src-noconflict/theme-github";
import "ace-builds/src-noconflict/keybinding-vim";
import "ace-builds/src-noconflict/keybinding-emacs";

const BACKEND_RENDER_URL = `${Settings.backend}/render`;
const customMode = new WSCMode();

enum State {
  Cool,
  Rendering,
}

interface Editor {
  [index: number]: { name: string; style: string };
}

const Editors: Editor = {
  0: { name: "Text", style: "" },
  1: { name: "Vim", style: "vim" },
  2: { name: "Emacs", style: "emacs" },
};

function Compose() {
  const [state, setState] = useState<State>();
  const [responseState, setResponseState] = useState<ResponseType>(
    ResponseType.RenderSuccess
  );
  const [errorMessage, setErrorMessage] = useState<string>("");
  const [editor, setEditor] = useState<number>(1);
  const [renderSpace, setRenderSpace] = useState<AceEditor | null>();
  const [fileName, setFileName] = useState<string>("my_song.socool");
  const [language, setLanguage] = useState<string>(template);

  const [render, setRender] = useState<boolean>(false);
  const [markers, setMarkers] = useState<IMarker[]>([]);

  const fileInput = useRef<HTMLInputElement>(null);

  const getStoredLanguage = () => {
    const stored = localStorage.getItem("language");
    if (stored) {
      setLanguage(stored);
    }
  };

  const typeFromResponse = (response: { string: any }) => {
    let responseType = Object.keys(response)[0];
    return responseType;
  };

  const valueFromResponse = (response: { string: any }) => {
    let value = Object.values(response)[0];
    return value;
  };

  useEffect(() => {
    const submit = async () => {
      if (render) {
        setMarkers([]);

        try {
          setState(State.Rendering);
          let response = await axios.post(BACKEND_RENDER_URL, { language });
          if (renderSpace) {
            localStorage.setItem("language", language);
            let responseType = typeFromResponse(response.data);
            let value = valueFromResponse(response.data);
            console.log(responseType);
            console.log(value);
            switch (responseType) {
              case ResponseType.RenderSuccess:
                setResponseState(responseType);
                setErrorMessage("");
                break;
              case ResponseType.ParseError:
                const n_lines = language.split("\n").length;
                displayError(value, n_lines, renderSpace, setMarkers);
                setResponseState(responseType);
                setErrorMessage(`Line: ${value.line} | Column ${value.column}`);
                break;
              case ResponseType.IdError:
                setResponseState(responseType);
                setErrorMessage(`${value.id}`);
                break;
              case ResponseType.IndexError:
                setResponseState(responseType);
                setErrorMessage(value.message);
                break;
              default:
                console.log("Unhandled error");
                console.log(response);
                break;
            }
          }
        } catch (err) {
          console.log(err);
        }

        setState(State.Cool);
        setRender(false);
      }
    };

    submit();
  }, [render, language, renderSpace]);

  useEffect(() => {
    if (renderSpace) {
      renderSpace.editor.getSession().setMode(customMode);
      renderSpace.editor.setTheme("ace/theme/wsc");
    }
  }, [renderSpace]);

  useEffect(() => {
    getStoredLanguage();
  }, []);

  const stopAudio = () => {
    //fadeOutSource(audioCtx, sources[(node + 1) % 2], gainNodes[(node + 1) % 2]);
  };

  const RenderState = () => {
    if (state === State.Rendering) {
      return <StateText>Rendering</StateText>;
    } else {
      return <div />;
    }
  };

  const loadFile = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e && e.target && e.target.files && e.target.files.length > 0) {
      let file = e.target.files[0];
      var reader = new FileReader();
      reader.onload = function (read_event: ProgressEvent<FileReader>) {
        if (
          read_event.target &&
          read_event.target.result &&
          typeof read_event.target.result === "string"
        ) {
          setFileName(file.name);
          setLanguage(read_event.target.result);
        }
      };

      reader.readAsText(file);
    }
  };

  const saveFile = useCallback(() => {
    var blob = new Blob([language], {
      type: "text/plain;charset=utf-8",
    });
    FileSaver.saveAs(blob, fileName);
  }, [fileName, language]);

  const handleLoad = () => {
    if (fileInput && fileInput.current) {
      fileInput.current.click();
    }
  };

  return (
    <Space>
      <Title>WereSoCool</Title>
      <SubTitle>Make cool sounds. Impress your friends/pets/plants.</SubTitle>

      <TopBox>
        <input
          ref={fileInput}
          type="file"
          accept=".socool"
          style={{ display: "none", visibility: "hidden" }}
          onChange={(e) => loadFile(e)}
        />

        <ButtonBox>
          <Button onClick={() => setRender(true)}>Render</Button>
          <Button onClick={() => stopAudio()}>Stop</Button>
          <Button onClick={handleLoad}>Load</Button>
          <Button onClick={saveFile}>Save</Button>
          <RenderState />
        </ButtonBox>

        <VimBox>
          <Button onClick={() => setLanguage(template)}>Reset</Button>
          <Button onClick={() => setEditor((editor + 1) % 3)}>
            {Editors[editor]["name"]}
          </Button>
        </VimBox>
      </TopBox>

      <AceEditor
        focus={true}
        ref={(el) => {
          setRenderSpace(el);
        }}
        placeholder="WereSoCool"
        mode="elixir"
        theme="github"
        name="aceEditor"
        keyboardHandler={Editors[editor]["style"]}
        value={language}
        onChange={(l) => setLanguage(l)}
        markers={markers}
        fontSize={20}
        showPrintMargin={true}
        showGutter={true}
        highlightActiveLine={true}
        setOptions={{
          showLineNumbers: true,
          tabSize: 2,
        }}
        commands={[
          {
            name: "submit",
            bindKey: { win: "Shift-Enter", mac: "Shift-Enter" },
            exec: () => {
              setRender(true);
            },
          },
          {
            name: "save",
            bindKey: { win: "Ctrl-s", mac: "Command-s" },
            exec: () => {
              saveFile();
            },
          },
          {
            name: "load",
            bindKey: { win: "Ctrl-l", mac: "Command-l" },
            exec: () => {
              handleLoad();
            },
          },
        ]}
        style={{
          height: "80vh",
          width: "80vw",
          marginLeft: "10vw",
        }}
      />
      <ErrorDescription
        errorMessage={errorMessage}
        responseState={responseState}
      />
    </Space>
  );
}

export default Compose;
